	module.exports = {"data":{"markdownRemark":{"html":"<p>You can integrate HackerOne with Jira Cloud.</p>\n<p>To integrate with Jira Cloud:</p>\n<ol>\n<li>Go to <strong>Settings > Program > Integrations</strong>.</li>\n<li>Click the <strong>Connect with JIRA</strong> link.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-326ad.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 8.739255014326648%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAACCAYAAABYBvyLAAAACXBIWXMAABYlAAAWJQFJUiTwAAAAVUlEQVQI13WMQQ6AMAgE+/+nmkhASrVFu9Ke7EGSzWyyZJJawcYF43rvk8UMzAxVhYjA/V72b3+C+awzWiqSe4vSlifLGbTvU0pEIfRf4eBhFyQy+AKWGZzJ0lfT9QAAAABJRU5ErkJggg=='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-1\"\n        title=\"\"\n        src=\"/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-e7ffa.png\"\n        srcset=\"/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-49813.png 125w,\n/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-dc8a5.png 250w,\n/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-e7ffa.png 500w,\n/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-dda24.png 750w,\n/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-c5f18.png 1000w,\n/static/jira-server-1-750c2d895bec403fb97069a27ca6d9b9-326ad.png 1396w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </li>\n<li>Choose <strong>JIRA Cloud</strong>, and you'll be redirected to Atlassian Marketplace where you can download the HackerOne for JIRA application.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-0ce80.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 496px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 45.56451612903226%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-server-3\"\n        title=\"\"\n        src=\"/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-c0270.png\"\n        srcset=\"/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-28d5f.png 125w,\n/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-47746.png 250w,\n/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-c0270.png 500w,\n/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-d97c0.png 750w,\n/static/jira-server-3-e570911e1a0dafd8cd591809f78ac120-0ce80.png 992w\"\n        sizes=\"(max-width: 496px) 100vw, 496px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </li>\n<li>Click the <strong>Get it now</strong> button in Atlassian Marketplace to install the HackerOne for Jira application. The page will redirect you to your Jira instance and the integration will be downloaded and installed.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-cloud-1-9da8a0cb49d2e66e46e74926290ef034-496e4.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 65.67460317460318%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-cloud-1\"\n        title=\"\"\n        src=\"/static/jira-cloud-1-9da8a0cb49d2e66e46e74926290ef034-e7ffa.png\"\n        srcset=\"/static/jira-cloud-1-9da8a0cb49d2e66e46e74926290ef034-49813.png 125w,\n/static/jira-cloud-1-9da8a0cb49d2e66e46e74926290ef034-dc8a5.png 250w,\n/static/jira-cloud-1-9da8a0cb49d2e66e46e74926290ef034-e7ffa.png 500w,\n/static/jira-cloud-1-9da8a0cb49d2e66e46e74926290ef034-dda24.png 750w,\n/static/jira-cloud-1-9da8a0cb49d2e66e46e74926290ef034-c5f18.png 1000w,\n/static/jira-cloud-1-9da8a0cb49d2e66e46e74926290ef034-22484.png 1500w,\n/static/jira-cloud-1-9da8a0cb49d2e66e46e74926290ef034-496e4.png 2016w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </li>\n<li>Click the <strong>Get started</strong> button on the installation confirmation pop-up.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-cloud-2-7250fd1fb0d9a550305cd84d2f476666-a2618.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 51.12903225806451%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-cloud-2\"\n        title=\"\"\n        src=\"/static/jira-cloud-2-7250fd1fb0d9a550305cd84d2f476666-e7ffa.png\"\n        srcset=\"/static/jira-cloud-2-7250fd1fb0d9a550305cd84d2f476666-49813.png 125w,\n/static/jira-cloud-2-7250fd1fb0d9a550305cd84d2f476666-dc8a5.png 250w,\n/static/jira-cloud-2-7250fd1fb0d9a550305cd84d2f476666-e7ffa.png 500w,\n/static/jira-cloud-2-7250fd1fb0d9a550305cd84d2f476666-a2618.png 620w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </li>\n<li>The installation process will continue on the HackerOne website and will ask you to select the program you want to link to. Select the program you want to link to your Jira instance.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-cloud-3-26e4820aa5aadb58bc1aeff3f20efbcc-5f060.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 457px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.95842450765865%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-cloud-3\"\n        title=\"\"\n        src=\"/static/jira-cloud-3-26e4820aa5aadb58bc1aeff3f20efbcc-5f060.png\"\n        srcset=\"/static/jira-cloud-3-26e4820aa5aadb58bc1aeff3f20efbcc-fced9.png 125w,\n/static/jira-cloud-3-26e4820aa5aadb58bc1aeff3f20efbcc-c2a1b.png 250w,\n/static/jira-cloud-3-26e4820aa5aadb58bc1aeff3f20efbcc-5f060.png 457w\"\n        sizes=\"(max-width: 457px) 100vw, 457px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </li>\n<li>The set up will take you to the Jira settings page where you can select which events you want to sync between HackerOne and Jira.</li>\n</ol>\n<p>With Jira integration, you can create Jira issues right from your HackerOne report.</p>\n<h3>Create Jira Issues from your HackerOne Report</h3>\n<p>To create Jira issues from your HackerOne report:</p>\n<ol>\n<li>In the comments section of your report, change <strong>Add comment</strong> to <strong>Change state > Triaged</strong>.</li>\n<li>Click the <strong>Create JIRA issue</strong> button to create the issue in Jira.</li>\n</ol>\n<p>Every time an action occurs on the Jira issue, Hackbot will automatically create an internal comment on the HackerOne report.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/jira-cloud-4-c879776b401409d1bd5fddefd2dee9be-c2133.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 22.712418300653596%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"jira-cloud-4\"\n        title=\"\"\n        src=\"/static/jira-cloud-4-c879776b401409d1bd5fddefd2dee9be-e7ffa.png\"\n        srcset=\"/static/jira-cloud-4-c879776b401409d1bd5fddefd2dee9be-49813.png 125w,\n/static/jira-cloud-4-c879776b401409d1bd5fddefd2dee9be-dc8a5.png 250w,\n/static/jira-cloud-4-c879776b401409d1bd5fddefd2dee9be-e7ffa.png 500w,\n/static/jira-cloud-4-c879776b401409d1bd5fddefd2dee9be-dda24.png 750w,\n/static/jira-cloud-4-c879776b401409d1bd5fddefd2dee9be-c5f18.png 1000w,\n/static/jira-cloud-4-c879776b401409d1bd5fddefd2dee9be-c2133.png 1224w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/programs/jira-cloud-integration.html","id":"programs/jira-cloud-integration","title":"Jira Cloud Integration","description":null}}},"pathContext":{}}

